import React from 'react';
import PropTypes from 'prop-types';
import { useStore } from '../StoreProvider';

function partial(props) {
    const { data, index, handleClick, selectedIndex, childRefs } = props;
    const {
        action,
        disp: { title, listMetadata },
    } = data;

    return (
        <div
            id={`${index}`}
            role="row"
            ref={childRefs}
            key={`result-programs-${index}`}
            aria-selected={selectedIndex === index}
            className="autocomplete-concierge__search-result-item"
            onClick={(e) => handleClick(e, { ...data, title })}
            onKeyDown={(e) => handleClick(e, { ...data, title })}
            tabIndex={-1}
        >
            <div className="autocomplete-results__details" role="gridcell">
                {listMetadata.roadworksStreet && (
                    <a title={listMetadata.roadworksStreet} className="autocomplete-results__title" href={action}>
                        {listMetadata.roadworksStreet}
                        {listMetadata.roadworksStartDate && (
                            <div className="autocomplete-results__metadata">
                                <svg className="svg-icon autocomplete-results__icon" aria-hidden="true">
                                    <use href="#time" />
                                </svg>
                                {listMetadata.roadworksStartDate}
                            </div>
                        )}
                        {listMetadata.roadworksContactTelephoneNumber && (
                            <div className="autocomplete-results__metadata">
                                <svg className="svg-icon autocomplete-results__icon" aria-hidden="true">
                                    <use href="#phone" />
                                </svg>
                                {listMetadata.roadworksContactTelephoneNumber}
                            </div>
                        )}
                        {listMetadata.roadworksLocality && (
                            <div className="autocomplete-results__metadata">
                                <svg className="svg-icon autocomplete-results__icon" aria-hidden="true">
                                    <use href="#map" />
                                </svg>
                                {listMetadata.roadworksLocality}
                            </div>
                        )}
                    </a>
                )}
            </div>
        </div>
    );
}

export default function Roadworks(props) {
    const { results, selectedIndex } = useStore();
    const { id, label, className, templateId, childRefs, handleClick } = props;

    if (!results[templateId] || results[templateId].length === 0) {
        return null;
    }

    return (
        <div id={`result-row-${id}`} className={[`autocomplete-concierge__section`, className].join(' ')} role="rowgroup">
            <h2 className="autocomplete-concierge__section-title">{label}</h2>
            {results[templateId] &&
                results[templateId].map((data, index) =>
                    partial({
                        data,
                        index,
                        handleClick,
                        selectedIndex,
                        childRefs,
                    })
                )}
        </div>
    );
}

const { string, number, func } = PropTypes;

Roadworks.propTypes = {
    id: string.isRequired,
    label: string,
    className: string,
    templateId: number.isRequired,
    childRefs: func.isRequired,
};

Roadworks.defaultProps = {
    label: ``,
    className: ``,
};
